.container {
  margin-bottom: 72px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 374px) {
  .container {
    width: 100vw;
  }
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }

  .page {
    color: var(--basic-text);
    background-color: #fafafa;
    margin: 0;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 128%;
  }
}

img {
  display: block;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    margin-bottom: 100px;
    padding-left: 32px;
    padding-right: 32px;
    overflow: hidden;
  }

  .container--subscribe {
    margin-bottom: 0;
    padding: 72px 150px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    margin-bottom: 128px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
  }

  .page {
    color: var(--basic-text);
    background-color: #fafafa;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 128%;
  }

  img {
    display: block;
  }
}

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 14px;
  padding-top: 20px;
  display: flex;
  position: relative;
}

.logo {
  letter-spacing: -.03em;
  align-items: center;
  font-style: italic;
  font-weight: 700;
  display: flex;
}

.logo--normal {
  font-style: normal;
}

.logo__icon {
  margin-left: 4px;
}

.navigation__link {
  position: relative;
}

.navigation__item {
  letter-spacing: -.02em;
  align-items: center;
  font-weight: 500;
  line-height: 114%;
}

.navigation__item:hover, .navigation__item :focus {
  color: #fd9222;
}

.navigation__item:hover :after, .navigation__item :focus :after {
  content: "";
  background-color: #fd9222;
  border-radius: 2px;
  width: 24px;
  height: 2px;
  display: inline-block;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.button__icon {
  stroke: #000;
  width: 28px;
  height: 28px;
}

.button__icon:hover .button__close, .button__icon :focus .button__close, .button__icon:hover .button__menu, .button__icon :focus .button__menu {
  stroke: #fd9222;
}

.button__icon.button__icon--active .button__menu {
  display: none;
}

.button__icon.button__icon--active .button__close {
  display: block;
}

.button-menu {
  stroke: #fff;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.button__close {
  stroke: #fff;
  display: none;
  position: absolute;
}

.button__close--active {
  display: block;
}

@media screen and (max-width: 767px) {
  .menu--content {
    color: #fff;
    background-color: #1e1823;
    width: 220px;
    height: 100vh;
    display: none;
    position: absolute;
    top: 0;
    right: 0;
  }

  .menu--content--active {
    display: block;
  }
}

header .navigation {
  margin-top: 136px;
  margin-left: 30px;
}

header .social {
  margin-top: auto;
}

header nav {
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 20px;
  padding-left: 20px;
  display: flex;
}

.social__icon {
  fill: #fff;
}

.social__icon:first-child {
  margin-right: 16px;
}

.social__icon:hover, .social__icon :focus {
  fill: #fd9222;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header-container {
    margin-bottom: 24px;
  }

  .menu--content {
    color: #fff;
    background-color: #1e1823;
    width: 290px;
    height: 100vh;
    display: none;
    position: absolute;
    top: 0;
    right: 0;
  }

  .menu--content--active {
    display: block;
  }

  header .navigation {
    margin-top: 156px;
    margin-left: 85px;
  }

  .navigation__item:not(:last-child) {
    margin-bottom: 24px;
  }

  .button-menu {
    top: 24px;
    right: 32px;
  }

  .button__icon {
    width: 32px;
    height: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .button-menu {
    display: none;
  }

  header .navigation {
    margin: 0 137px 0 0;
    display: flex;
  }

  .header-container {
    height: 100%;
    margin-bottom: 0;
    padding: 0 32px;
  }

  .header nav {
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    display: flex;
  }

  .navigation__item {
    padding-top: 28px;
    padding-bottom: 32px;
  }

  .navigation__item:not(:last-child) {
    margin-bottom: 0;
  }

  header .social__icon {
    fill: #000;
  }

  .logo {
    font-size: 18px;
  }

  header .social {
    margin-top: 0;
  }
}

:root {
  --basic-text: #111;
  --secondary-dark-text: #111111b3;
  --secondary-white-text: #ffffffb3;
  --first-white-text: #fff;
  --basic-title: #fff;
  --accent-text: #fd9222;
  --basic-background: #fafafa;
  --secondary-background: #1e1823;
}

ul, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hero {
  color: var(--first-white-text);
  background: linear-gradient(#1110 0%, #1116 100%), url("chocolate1.52cff4f7.jpg");
  border-radius: 30px;
  padding: 395px 20px 20px;
}

.hero__title {
  letter-spacing: -.03em;
  margin-bottom: 32px;
  font-size: 20px;
  font-weight: 500;
  line-height: 120%;
}

.button--hero {
  display: flex;
}

.button__form {
  background-color: var(--accent-text);
  color: currentColor;
  border: none;
  border-radius: 100px;
  height: 41px;
  padding: 12px 28px;
  font-weight: 600;
  line-height: 121%;
}

.button__form--buy {
  margin-right: 14px;
}

.button__form--buy:hover, .button__form--buy :focus {
  color: var(--accent-text);
  background-color: #fcf1e0;
}

.button__form--how {
  opacity: 1;
  background-color: #fff0;
  border: 1px solid #fff;
}

.button__form--how:hover, .button__form--how :focus {
  color: var(--accent-text);
  border-color: var(--accent-text);
}

.pointer {
  letter-spacing: -.03em;
  font-size: 12px;
  font-weight: 500;
  line-height: 117%;
}

.pointer__icon {
  fill: var(--accent-text);
  background: #fff;
  border-radius: 28px;
  width: 28px;
  height: 28px;
  margin-left: 12px;
  padding: 6px;
}

.pointer--container {
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.pointer--container:hover .pointer, .pointer--container :focus .pointer {
  color: var(--accent-text);
}

.pointer--container:hover .pointer__icon, .pointer--container :focus .pointer__icon {
  fill: #fff;
  background-color: var(--accent-text);
}

.button__item {
  text-align: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .hero {
    background: linear-gradient(#1110 0%, #1116 100%), url("chocolate1.996b8ded.jpg");
    padding: 671px 40px 40px;
  }

  .hero__title {
    width: 484px;
    font-size: 28px;
    line-height: 122%;
  }

  .hero-container {
    margin-bottom: 50px;
  }

  .nav-hero {
    place-content: baseline space-between;
    align-items: center;
    display: flex;
  }

  .pointer {
    font-size: 14px;
  }

  .pointer--container {
    margin-top: 0;
  }

  .pointer__icon {
    width: 38px;
    height: 38px;
    margin-left: 14px;
  }

  .button__form {
    padding: 14px 40px;
  }
}

@media screen and (min-width: 1200px) {
  .hero {
    background: linear-gradient(#1110 0%, #1116 100%), url("chocolate1.a64445c0.jpg");
    padding: 381px 40px 40px;
  }

  .hero-container {
    margin-bottom: 72px;
  }
}

.preference {
  background-color: #1e1823;
}

.preference--container {
  padding-top: 20px;
  padding-bottom: 36px;
}

.preference-general {
  display: none;
}

.preference-list {
  color: var(--first-white-text);
}

.preference-text {
  color: #ffffffb3;
  letter-spacing: -.02em;
  padding-right: 28px;
}

.preference-icon {
  border: 1px solid #ffffff80;
  border-radius: 365px;
  width: 64px;
  height: 64px;
  margin-bottom: 22px;
  padding: 15px;
}

.preference-title {
  margin-bottom: 18px;
}

.preference-item:not(:last-child) {
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .preference--container {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .preference-list {
    flex-wrap: wrap;
    gap: 32px;
    display: flex;
  }

  .preference-item {
    flex-basis: calc(50% - 16px);
  }

  .preference-item:not(:last-child) {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1200px) {
  .preference-item {
    flex-basis: calc(33.3333% - 23.3333px);
    position: relative;
  }

  .preference-item:not(:last-child) {
    margin-right: 70px;
    padding-right: 18px;
  }

  .preference-item:last-child {
    flex-basis: calc(33.3333% - 41.3333px);
  }

  .preference-item:not(:last-child):after {
    content: " ";
    border-right: 1px solid #ffffff80;
    width: 2px;
    height: 124px;
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }

  .preference--container {
    padding: 50px;
  }

  .preference-list {
    flex-wrap: nowrap;
    gap: 0;
  }

  .preference-text {
    padding-right: 0;
  }
}

.taste__title {
  text-align: center;
  letter-spacing: -.03em;
  text-transform: uppercase;
  width: 283px;
  margin: auto auto 24px;
  font-size: 20px;
  font-weight: 600;
  line-height: 120%;
}

.taste__title--accent {
  color: var(--accent-text);
}

.taste__text {
  text-align: center;
  letter-spacing: -.02em;
  margin-bottom: 32px;
}

.taste__item {
  transition: box-shadow 2.5s, all cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.taste__item:not(:last-child) {
  margin-bottom: 20px;
}

@media screen and (max-width: 767px) {
  .taste__item--white--second {
    display: none;
  }
}

.taste__name {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: var(--basic-title);
